body {
  color: #440505;
  font-family: Montserrat, sans-serif;
}

.breed-select {
  background-color: #0ff;
  border: none;
  border-bottom: 2px solid #000;
  outline: none;
  margin-bottom: 50px;
  padding: 5px;
  font-size: 16px;
  position: absolute;
  top: 10px;
  left: 10px;
}

.breed-select:hover {
  border-bottom: 2px solid #021e27;
}

.cat-info {
  margin-left: 200px;
  display: flex;
}

.cat-info img {
  margin-left: 50px;
  margin-right: 30px;
}

.loader {
  width: 130px;
  height: 48px;
  box-sizing: border-box;
  background-color: #0000;
  background-image: linear-gradient(45deg, #000 25%, #0000 25% 75%, #000 75% 100%), linear-gradient(45deg, #000 25%, #fff 25% 75%, #000 75% 100%);
  background-position: 0 0, 16px 16px;
  background-repeat: repeat, repeat;
  background-size: 32px 32px;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  font-size: 10px;
  animation: raceBoard .6s linear infinite;
  display: inline-block;
}

@keyframes raceBoard {
  0% {
    background-position: 0 0, 16px 16px;
  }

  100% {
    background-position: 32px 0, 48px 16px;
  }
}

